<template>
  <v-menu
    offset-x
    transition="scale-transition"
    :close-on-content-click="false"
    bottom
    left
    origin="top right"
    nudge-left="8"
  >
    <template v-slot:activator="{ on, attrs }">
      <v-card
        class="settings-card pa-2"
        v-bind="attrs"
        v-on="on"
        color="rgba(88,88,88, 0.3)"
        min-width="100"
        link
      >
        <v-icon large>mdi-cogs</v-icon>
      </v-card>
    </template>

    <v-card class="text-center" width="300">
      <v-card-text>
        <strong class="d-inline-block">主题设置面板</strong>

        <v-divider class="my-4 accent"></v-divider>
        <v-row align="center">
          <v-col cols="auto"> Dark Mode </v-col>
          <v-spacer></v-spacer>
          <v-col cols="auto">
            <v-switch
              v-model="$vuetify.theme.dark"
              class="ma-0 pa-0"
              hide-details
              color="accent"
            >
            </v-switch>
          </v-col>
        </v-row>
        <v-divider class="my-4 accent"></v-divider>

        <v-item-group class="d-flex justify-space-between" v-model="color">
          <v-item v-for="item in colors" :key="item" :value="item">
            <template v-slot="{ active, toggle }">
              <v-avatar
                size="25"
                :color="item"
                class="settings-item"
                @click="toggle"
                :class="
                  active &&
                  ($vuetify.theme.dark
                    ? 'settings-item-active-dark'
                    : 'settings-item-active-light')
                "
              >
              </v-avatar>
            </template>
          </v-item>
        </v-item-group>

        <v-divider class="my-4 accent"></v-divider>
      </v-card-text>
    </v-card>
  </v-menu>
</template>

<script>
export default {
  name: "DashboardSettings",
  data: () => ({
    color: "#1976D2",
    colors: [
      "#1976D2",
      "#424242",
      "#82B1FF",
      "#FF5252",
      "#2196F3",
      "#4CAF50",
      "#FFC107",
    ],
  }),
  // 观察属性
  watch: {
    //此处监测的变量是 color,当其值发生变化时，执行内部代码
    color(val) {
      this.$vuetify.theme.themes[
        this.$vuetify.theme.dark ? "dark" : "light"
      ].accent = val;
    },
  },
};
</script>

<style scoped>
.settings-card {
  position: fixed;
  top: 215px;
  right: -40px;
  border-radius: 8px;
}

.settings-item {
  cursor: pointer;
  border-width: 3px;
  border-style: solid;
  border-color: transparent !important;
}

.settings-item-active-dark {
  border-color: whitesmoke;
}

.settings-item-active-light {
  border-color: black;
}
</style>